import {
  Drawer,
  Form,
  Button,
  Col,
  Radio,
  Row,
  Input,
  message,
  Checkbox,
} from "antd";
import UserApi from "api/user-api";
import React, { useState, useEffect } from "react";
import { Select } from "antd";
import * as yup from "yup";
import UserSelector from "views/app-views/shared/userSelector";

const { Option } = Select;

const TaskFormdit = (props) => {
  const [isValidForm, setIsValidForm] = useState(false);
  const [item, setItem] = useState({ id: 0 });
  const [isLoading, setIsLoading] = useState(false);
  const [isLoading1, setIsLoading1] = useState(false);
  const [form] = Form.useForm();
  const [editSubtask, setEditSubTask] = useState(false);

  let hasPasswordSchema = yup.object().shape({
    firstName: yup.string().required("Tên không được để trống."),
    hasPassword: yup.boolean(),
    lastName: yup.string().required("Họ không được để trống."),
    userName: yup.string().required("Tên đăng nhập không được để trống."),
    passwordHash: yup
      .string()
      .required("Mật khẩu không được để trống.")
      .min(6, "Mật khẩu it nhất có 6 ký tự."),
  });

  let noPasswordSchema = yup.object().shape({
    firstName: yup.string().required("Tên không được để trống."),
    hasPassword: yup.boolean(),
    lastName: yup.string().required("Họ không được để trống."),
    userName: yup.string().required("Tên đăng nhập không được để trống."),
  });

  const yupSync = {
    async validator({ field }, value) {
      if (item.hasPassword) {
        await hasPasswordSchema.validateSyncAt(field, { [field]: value });
      } else {
        await noPasswordSchema.validateSyncAt(field, { [field]: value });
      }
    },
  };

  useEffect(() => {
    if (props.id != 0) {
      // new UserApi().getUserProfile(props.id).then((response) => {
      //   setItem(response.data);
      // });
    }
  }, [props.id]);

  useEffect(() => {
    validateForm();
  }, [item]);

  const handleChange = (event) => {
    validateForm();
    const currentItem = Object.assign({}, item);
    const targetValue =
      event.target.type === "checkbox"
        ? event.target.checked
        : event.target.value;
    currentItem[event.target.name] = targetValue;
    setItem(currentItem);
  };

  const addSub = () => {
    setEditSubTask(true);
    if (props.onAddSub) {
      props.onAddSub();
    }
  };

  const addOrRemoveRole = (checked, role) => {
    let currentItem = item;
    currentItem.roles = item.roles || [];
    if (checked && currentItem.roles.indexOf(role) < 0) {
      currentItem.roles.push(role);
    }
    if (!checked && currentItem.roles.indexOf(role) >= 0) {
      currentItem.roles.splice(currentItem.roles.indexOf(role), 1);
    }
    setItem({ ...currentItem });
  };

  const validateForm = () => {
    form
      .validateFields(["firstName", "lastName", "userName", "passwordHash"])
      .then((values) => {
        setIsValidForm(true);
      })
      .catch((errorInfo) => {
        if (
          errorInfo &&
          errorInfo.errorFields &&
          errorInfo.errorFields.length > 0
        ) {
          setIsValidForm(false);
        }
      });
  };

  const resetForm = () => {
    let cloneItem = { ...item };
    cloneItem.userName = "";
    cloneItem.firstName = "";
    cloneItem.lastName = "";
    cloneItem.email = "";
    cloneItem.mobile = "";
    cloneItem.address = "";
    setItem({ ...cloneItem });
  };

  const onAdd = (isContinue) => {
    validateForm();

    setIsLoading(isContinue ? false : true);
    setIsLoading1(isContinue ? false : true);

    const currentItem = Object.assign({}, item);
    new UserApi()
      .save(currentItem)
      .then((response) => {
        setIsLoading(false);
        setIsLoading1(false);
        message.success("Cập nhật thành công.");
        if (props.onSaved) {
          props.onSaved();
        }
        if (!isContinue) {
          props.onClose();
        } else {
          resetForm();
        }
      })
      .catch((error) => {
        var errors = [];
        Object.keys(error.response.data).forEach((key) => {
          errors.push({ name: key, errors: error.response.data[key] });
        });
        form.setFields(errors);
        setIsLoading(false);
        setIsLoading1(false);
      });
  };

  return (
    <Form
      form={form}
      layout="vertical"
      hideRequiredMark
      autoComplete="off"
      initialValues={item}
    >
      <Row gutter={8}>
        <Col span={24}>
          <Form.Item name="lastName" rules={[yupSync]} label="Tên">
            <Input
              name="lastName"
              placeholder=""
              value={item.lastName}
              onChange={(event) => {
                handleChange(event);
              }}
            />
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item
            name="lastName"
            rules={[yupSync]}
            label="Dự án hoặc công việc cha"
          >
            <Input
              name="lastName"
              placeholder=""
              value={item.lastName}
              onChange={(event) => {
                handleChange(event);
              }}
            />
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Loại công việc">
            <Radio.Group defaultValue="a" buttonStyle="solid">
              <Radio.Button value="a">Việc cá nhân</Radio.Button>
              <Radio.Button value="b">Giao việc</Radio.Button>
            </Radio.Group>
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Trạng thái công việc">
            <Select
              onChange={(value) => {
                const currentItem = Object.assign({}, item);
                currentItem.gender = parseInt(value);
                setItem(currentItem);
              }}
              name="gender"
              value={item.gender}
              defaultValue={item.gender}
              placeholder=""
            >
              <Option key={"urgent-2"} value={0}>
                Nháp
              </Option>
              <Option key={"urgent-1"} value={-1}>
                Đang diễn ra
              </Option>
              <Option key={"urgent-3"} value={1}>
                Hoàn thành
              </Option>
              <Option key={"urgent-3"} value={1}>
                Quá hạn
              </Option>
            </Select>
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Hạn hoàn thành">
            <Input
              name="address"
              value={item.address}
              onChange={handleChange}
              placeholder=""
            />
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Độ ưu tiên">
            <Select
              onChange={(value) => {
                const currentItem = Object.assign({}, item);
                currentItem.gender = parseInt(value);
                setItem(currentItem);
              }}
              name="gender"
              value={item.gender}
              defaultValue={item.gender}
              placeholder=""
            >
              <Option key={"urgent-2"} value={0}>
                Thấp
              </Option>
              <Option key={"urgent-1"} value={-1}>
                Trung bình
              </Option>
              <Option key={"urgent-3"} value={1}>
                Cao
              </Option>
            </Select>
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Người thực hiện">
            <UserSelector />
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Mô tả">
            <Input.TextArea
              name="phoneNumber"
              rows={6}
              value={item.phoneNumber}
              onChange={handleChange}
              placeholder=""
            />
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Công việc con"></Form.Item>
          <Button onClick={addSub}>Them</Button>
        </Col>
      </Row>
    </Form>
  );
};

export default TaskFormdit;
